.information {
  background-image: url("../img/traveller-illustration-mobile@1x.png"),
    url("../img/globe-back-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-size: 192px 255px, 100% 480px;
  background-position: 62% 223px, top;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/traveller-illustration-mobile@2x.png"),
      url("../img/globe-back-mobile@2x.jpg");
  }

  @media (min-width: @tablet-width) {
    background-image: url("../img/traveller-illustration-tablet@1x.png"),
      url("../img/globe-back-tablet@1x.jpg");
    background-size: 372px 451px, 100% 612px;
    background-position: 65% 240px, center 220px;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/traveller-illustration-tablet@2x.png"),
        url("../img/globe-back-tablet@2x.jpg");
    }
  }

  @media (min-width: @desktop-width) {
    background-image: url("../img/traveller-illustration-desktop@1x.png"),
      url("../img/globe-back-desktop@1x.jpg");
    background-size: 431px 640px, 1800px 750px;
    background-position: 60% 62px, -180px -45px;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/traveller-illustration-desktop@2x.png"),
        url("../img/globe-back-desktop@2x.jpg");
    }
  }
}

.information__wrapper {
  padding: 0 25px;
  padding-top: 90px;
  margin-bottom: -202px;

  @media (min-width: @tablet-width) {
    margin-top: 162px;
    padding: 0 40px;
    margin-bottom: 0;
  }

  @media (min-width: @desktop-width) {

    margin-top: 0;
    padding: 0 60px;
    padding-top: 157px;
    padding-bottom: 123px;
  }
}

.information__header {
  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 296px 110px 75px;
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: 420px 1fr 290px;
    grid-template-rows: 95px 120px;
  }
}

.information__slogan {
  font-size: 36px;
  line-height: 40px;
  font-weight: bold;
  color: @white;
  text-align: center;
  max-width: 240px;
  margin: 0 auto;
  margin-bottom: 315px;

  @media (min-width: @tablet-width) {
    font-size: 60px;
    line-height: 70px;
    max-width: 400px;

    grid-column-start: 1;
    grid-column-end: 3;
  }

  @media (min-width: @desktop-width) {
    text-align: left;
    margin-left: 68px;
    max-width: 420px;


    grid-column-start: 1;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 3;
  }
}

.information__quote {
  font-size: 16px;
  line-height: 22px;
  font-weight: lighter;
  color: @white;
  margin-left: 25px;
  max-width: 165px;
  margin-bottom: 18px;

  @media (min-width: @tablet-width) {
    font-size: 28px;
    line-height: 33px;
    max-width: 265px;

    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 2;
    grid-row-end: 3;
  }

  @media (min-width: @desktop-width) {
    font-size: 28px;
    line-height: 32px;
    font-weight: 300;
    margin: 0;
    margin-top: 13px;
    margin-left: 2px;

    grid-column-start: 3;
    grid-column-end: 4;
    grid-row-start: 1;
    grid-row-end: 2;
  }
}

.information__author {
  font-size: 16px;
  line-height: 22px;
  font-weight: bold;
  color: @white;
  margin-left: 25px;
  max-width: 165px;
  margin-bottom: 30px;

  @media (min-width: @tablet-width) {
    font-size: 21px;
    line-height: 25px;
    max-width: 255px;

    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 3;
    grid-row-end: 4;
  }

  @media (min-width: @desktop-width) {
    margin: 0;
    margin-top: 27px;
    margin-left: 4px;

    grid-column-start: 3;
    grid-column-end: 4;
    grid-row-start: 2;
    grid-row-end: 3;
  }
}

.information__list {
  margin: 0;
  padding: 0;
  list-style: none;

  @media (min-width: @tablet-width) {
    margin-top: 210px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
  }

  @media (min-width: @desktop-width) {
    margin-top: 100px;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr;
  }
}

.information__description {
  font-size: 18px;
  line-height: 18px;
  font-weight: bold;
  color: @light-black;
  margin: 0;
  margin-top: 20px;
  margin-left: 24px;

  @media (min-width: @tablet-width) {
    font-size: 24px;
    line-height: 24px;
    margin-top: 30px;
    margin-left: 33px;
  }

  @media (min-width: @desktop-width) {
    margin-top: 28px;
    margin-left: 30px;
  }
}

.information__number {
  font-size: 18px;
  line-height: 18px;
  font-weight: bold;
  margin: 0;
  margin-top: 20px;
  margin-right: 21px;
  color: @white;

  @media (min-width: @tablet-width) {
    font-size: 30px;
    line-height: 30px;
    margin-left: 33px;
    margin-bottom: 21px;
  }

  @media (min-width: @desktop-width) {
    font-size: 24px;
    line-height: 24px;
    margin-top: 28px;
    margin-right: 50px;
  }
}

.information__item {
  position: relative;
  width: auto;
  height: 160px;
  border-radius: 10px;
  display: flex;
  justify-content: space-between;

  @media (min-width: @tablet-width) {
    height: 140px;
    border-radius: 0;
    flex-direction: column;
  }

  @media (min-width: @desktop-width) {
    height: 105px;
    flex-direction: row;
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;
  }

  &--orange {
    background-color: @orange;
    z-index: 1;

    @media (min-width: @tablet-width) {
      border-top-left-radius: 15px;
    }
  }

  &--orange-light {
    background-color: @orange-light;
    z-index: 2;
    top: -80px;


    @media (min-width: @tablet-width) {
      border-top-right-radius: 15px;
      border-top-left-radius: 15px;
      z-index: 3;
      top: 0;
      left: -12px;
    }

    @media (min-width: @desktop-width) {
      z-index: 2;
      margin-left: -2px;
      margin-right: -29px;
    }
  }

  &--yellow-dark {
    background-color: @yellow-dark;
    z-index: 3;
    top: -160px;


    @media (min-width: @tablet-width) {
      border-bottom-left-radius: 15px;
      z-index: 2;
      top: 0;
    }

    @media (min-width: @desktop-width) {
      z-index: 3;
      left: 35px;
      margin-left: -34px;
    }
  }

  &--yellow {
    background-color: @yellow;
    height: 80px;
    z-index: 4;
    top: -240px;

    @media (min-width: @tablet-width) {
      height: 140px;
      border-bottom-right-radius: 15px;
      border-bottom-left-radius: 15px;
      top: 0;
      left: -12px;
    }

    @media (min-width: @desktop-width) {
      height: 105px;
      border-top-right-radius: 15px;
      width: 315px;
      left: 15px;
    }
  }
}
